.askquestion {
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto;
  width: 50%;
  background-color: white;
  border-radius: 15px;
  box-shadow: 0 1px 2px hsla(0, 0%, 0%, 0.05), 0 1px 4px hsla(0, 0%, 0%, 0.05),
    0 2px 8px hsla(0, 0%, 0%, 0.05);
  margin-top: 10px;
  margin-bottom: -5px;
  font-family: "Nunito", sans-serif;
  font-weight: 600;
  font-size: 20px;
  color: rgb(50, 63, 177);

  div {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    width: 90%;
    height: 100%;

    img {
      border-radius: 999px;
      display: flex;
      justify-content: center;
      align-items: center;
      box-shadow: 0 1px 2px hsla(0, 0%, 0%, 0.397),
        0 1px 4px hsla(0, 0%, 0%, 0.274), 0 2px 8px hsla(0, 0%, 0%, 0.05);
      width: 48px;
      height: 48px;
      text-align: center;
      font-family: inherit;
      font-size: 32px;
      font-weight: 700;
      margin-right: 10px;
    }
    .question-input {
      background-color: rgba(166, 204, 236, 0.25);
      height: 70%;
      width: 90%;
      border-radius: 15px;
      padding-left: 10px;
    }
    input {
      border: none;
      height: 100%;
      width: 90%;
      background-color: rgba(255, 255, 255, 0);
      font-family: "Nunito", sans-serif;
      font-weight: 600;
      font-size: 16px;
      padding-left: 10px;
    }
    textarea:focus,
    input:focus {
      outline: none;
    }
  }
}
