.question-post {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-top: 20px;

  .post-box {
    background-color: #fff;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    margin-bottom: 15px;
    border-radius: 5px;
    width: 75%;

    h2 {
      font-family: "Nunito", sans-serif;
      font-size: 18px;
      line-height: 22px;
      font-weight: 500;
      color: #1c1c1c;
      text-align: left;
      padding-left: 20px;
      margin-left: 10px;
    }
  }

  form {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 100%;

    div {
      width: 100%;
    }

    .post-title {
      width: 95%;
      position: relative;
      border: #48484b56 1px solid;
      border-radius: 5px;
      flex-direction: column;

      &:focus {
        box-shadow: none;
        border: 1px solid #1a1a1b;
        outline: none;
      }

      #charcount {
        font-size: 10px;
        font-weight: 700;
        letter-spacing: 0.5px;
        line-height: 12px;
        text-transform: uppercase;
        color: #878a8c;
        pointer-events: none;
        position: absolute;
        top: 16px;
        right: -45%;
        font-family: "Nunito", sans-serif;
      }

      #question-title {
        overflow-x: hidden;
        overflow-wrap: break-word;
        height: 40px;
        outline: none;
        resize: none;
        width: 95%;
        border: none;
        padding: none;
        font-family: "Nunito", sans-serif;
        font-size: 16px;
        font-weight: 500;

        &::placeholder {
          margin: auto;
          font-family: "Nunito", sans-serif;
          display: flex;
          justify-content: center;
        }
      }
    }

    .post-body {
      flex-direction: column;
      width: 95%;
      border: #48484b56 1px solid;
      border-radius: 5px;
      margin: 10px;

      &:focus {
        box-shadow: none;
        border: 1px solid #1a1a1b;
        outline: none;
      }

      #question-body {
        overflow-x: hidden;
        overflow-wrap: break-word;
        outline: none;
        resize: block;
        width: 95%;
        border: none;
        outline: none;
        font-family: "Nunito", sans-serif;
        font-size: 16px;
        font-weight: 500;
        margin: 10px;
        // border: black 1px solid;

        &::placeholder {
          font-family: "Nunito", sans-serif;
          display: flex;
          justify-content: center;
        }
      }
    }

    .company-info {
      width: 95%;
      justify-content: space-between;
      margin: 10px;

      input {
        height: 40px;
        width: 47%;
        border: #48484b56 1px solid;
        border-radius: 5px;
        font-family: "Nunito", sans-serif;
        font-size: 16px;
        font-weight: 500;
        padding-left: 20px;

        &::placeholder {
          font-family: "Nunito", sans-serif;
          display: flex;
          justify-content: center;
        }

        &:focus {
          box-shadow: none;
          border: 1px solid #1a1a1b;
          outline: none;
        }
      }
    }
  }

  .btn-div {
    height: 40px;
    width: 95%;
    // border: #1a1a1b solid 1px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin: 10px;
    button {
      height: 90%;
      margin-top: auto;
      margin-bottom: auto;
      background: none;
      color: white;
      border: none;
      padding: 0;
      font: inherit;
      cursor: pointer;
      outline: inherit;
      background-color: hsl(203, 86%, 44%);
      width: 100px;
      border-radius: 5px;
      font-family: "Nunito", sans-serif;
    }
  }

  .add-tag {
    width: 95%;
    flex-direction: column;
    // border: solid 1px black;
    align-items: flex-start;

    h3 {
      // border: solid 1px blue;
      font: inherit;
      font-family: "Nunito", sans-serif;
      // align-self: flex-start;
      // justify-self: center;
    }

    div {
      display: flex;
      flex-direction: row;
      justify-content: flex-start;
      align-items: center;
      border: #48484b56 1px solid;
      border-radius: 5px;
      width: 20%;
      padding-left: 10px;
      height: 45px;

      .icn {
        font-size: 24px;
        color: #929292;
        &:hover {
          cursor: pointer;
        }
      }
    }
    .tag-input {
      width: 30%;
    }

    input {
      height: 35px;
      width: 100%;
      font-family: "Nunito", sans-serif;
      font-size: 16px;
      font-weight: 500;
      padding-left: 10px;
      border: none;
      margin: auto;

      &::placeholder {
        font-family: "Nunito", sans-serif;
        display: flex;
        justify-content: center;
      }

      &:focus {
        box-shadow: none;
        // border: 1px solid #1a1a1b;
        outline: none;
      }
    }

    p {
      font-style: italic;
      font-family: "Nunito", sans-serif;
      font-size: 12px;
      font-weight: 200;
      margin-left: 30px;
    }
  }

  .tags {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    width: max-content;
    align-self: flex-start;
    margin-left: 40px;

    .tag {
      margin-right: 10px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      background-color: #0030b6;
      height: 40px;
      border-radius: 999px;

      .icn {
        color: white;
        margin-top: 2px;
        margin-left: 5px;
        margin-right: 10px;
      }

      span {
        text-transform: lowercase;
        font-family: "Nunito", sans-serif;
        color: white;
        margin: auto 0px auto 20px;
      }
    }
  }
  .close-icn {
    &:hover {
      cursor: pointer;
    }
  }
}
